import React from "react";
import { Stack, Typography } from "@mui/material";
import { ArabicToPersian } from "utils/ConvertToPersianTextFromArabic";

const Message = ({ message }) => {
  return (
    <React.Fragment>
      <Stack spacing={2}>
        <Typography variant="body1" textAlign="center">
          {ArabicToPersian(message?.Onvan)}
        </Typography>
        <Typography variant="body2" textAlign="center">
          {ArabicToPersian(message?.Messeage)}
        </Typography>
        <Stack direction="row" spacing={2} justifyContent="flex-end">
          <Typography variant="body2" textAlign="center">
            {ArabicToPersian(message?.TimeSend)}
          </Typography>
          <Typography variant="body2" textAlign="center">
            {ArabicToPersian(message?.DateSend)}
          </Typography>
        </Stack>
      </Stack>
    </React.Fragment>
  );
};

export default Message;
